/**
 * ID
 * @returns {Promise}
 */
export const getUsersInfoByIds = async (_userIds = []) => {
  const userIds = Array.from(new Set(_userIds.filter(id => !!id)));
  if (!userIds.length) {
    const data = new Array(_userIds.length);
    data.fill(null);
    return { data };
  }
  const res = await axios({
    method: 'post',
    url: '/authcenter/user/im/info',
    data: userIds,
  });
  const users = Object.fromEntries(res.data.map(user => ([user.id, user])));
  res.data = _userIds.map(id => users[id] ?? null);
  return res;
};

